
#svg-wrapper{margin:auto;width:100%
}
#svg-wrapper img{padding:20px;width:600px;margin:auto
}
.Metabolite .Shape,.Metabolite .Label,.Reaction .Shape,.Reaction .Label{cursor:pointer
}
.Metabolite:hover .Shape path,.Reaction:hover .Shape path{fill:red
}
.svgbox{position:relative;margin:auto;width:auto;height:700px;border:1px solid black
}
#svgOption{position:absolute;top:10px;left:10px;width:50px;height:30px;z-index:10
}
#svgOption span{display:inline-block;margin-right:5px
}
svg .hl{fill:#22FFFF
}



