
#search{height:38px
}
#input-wrapper{position:relative
}
#text-input-alert{width:150px;height:38px;position:absolute;top:10px;right:0;color:lightgrey
}
#searchResults{background:white;position:absolute;top:40px;max-height:300px;overflow-y:auto;width:100%;border:1px solid #64CC9A;border-top:0;margin-top:-2px;z-index:30
}
#searchResults .searchGroupResultSection:first-child{padding-top:15px
}
#searchResults .searchResultSection{margin-bottom:10px;padding:0 10px;background:white
}
#searchResults .searchResultSection label{font-style:italic
}
#searchResults .searchResultSection span{cursor:pointer
}
#searchResults .searchGroupResultSection:last-child .searchResultSection:last-child hr{display:none
}



